.page {
  display: flex;
  justify-content: center;
  padding-top: 6rem;
  height: 100%;
  width: 100%;
  background-color: #100F11;
}

.pageContent {
  display: flex;
  justify-self: center;
  height: 100%;
  width: 85%;
}

.pageContent_Left {
  display: flex;
  position: fixed;
  flex-direction: column;
  justify-content: flex-end;
  left: 2%;
  bottom: 0;
}

.pageContent_Right {
  display: flex;
  position: fixed;
  flex-direction: column;
  justify-content: flex-end;
  right: 2%;
  bottom: 0;
}

.pageContent_Center {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  align-items: center;
  gap: 3rem;
}

.pageContent_Left, .pageContent_Right hr {
  width: 5rem;
}

.sideButtonSection {
  display: flex;
  flex-direction: column;
  align-self: center;
  max-width: 50%;
  margin-bottom: 70%;
  gap: 1rem;
}

.sideEmail {
  display: flex;
  align-self: center;
  margin-bottom: 100%;
  font-size: 1.75rem;
  -ms-writing-mode: tb-lr;
  writing-mode: vertical-lr;
}

.sideButton {
  display: flex;
  align-self: center;
  padding: 0.5rem;
  background-color: #850404;
}

.sideButton img {
  width: 2.5rem;
}

.sideLine {
  transform: rotateZ(90deg);
  margin-bottom: 50%;
  border-color: #850404;
  border-width: 0.15rem;
}

.pageTitle {
  display: grid;
  grid-template-columns: 1fr 2.5fr;
  justify-content: center;
  width: 80%;
  text-align: right;
  grid-gap: 2rem;
}

.pageTitle span {
  font-size: 2rem;
  font-family: Inconsolata-Bold;
}

.hrline {
  border-color: #850404;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom-width: 0.2rem;
} /*# sourceMappingURL=PageSample.css.map */
#navbar {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  align-items: center;
  width: 100%;
  height: 5rem;
  justify-content: center;
  font-size: 1.35rem;
  background-color: #100F11;
  z-index: 100;
}

#navbar-content {
  display: flex;
  align-items: center;
  width: 95%;
  height: 100%;
  justify-content: space-between;
}

#logoSection {
  padding: 0rem 2rem;
  font-size: 2rem;
  font-family: Inconsolata-Bold;
}

#navSelectionSection {
  display: flex;
  gap: 3rem;
  padding: 0rem 2rem;
  align-items: center;
}

#navOptions {
  display: flex;
  list-style: none;
  gap: 2rem;
}

#resumeButton {
  padding: 1rem 1.5rem;
  font-size: 1.35rem;
}

#landingPageContent {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 95%;
  height: 80%;
  align-content: center;
}

#checkOutProjectButton {
  display: flex;
  padding: 1.5rem 2rem;
  font-size: 1.5rem;
}

#welcomeMessage {
  display: flex;
  justify-content: center;
}

#welcomeMessageContent {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 90%;
  font-size: 2.35rem;
  justify-content: center;
}

#welcomeMessageContent p {
  font-size: 1.7rem;
}

#welcomeImage {
  display: flex;
  justify-content: center;
}

#welcomeImage img {
  max-width: 80%;
}/*# sourceMappingURL=LandingPage.css.map */