@use "./PageSample.css" as *;



#navbar {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    align-items: center;
    width: 100%;
    height: 5rem;
    justify-content: center;
    font-size: 1.35rem;
    background-color: #100F11;
    z-index: 100;
}

#navbar-content {
    display: flex;
    align-items: center;
    width: 95%;
    height: 100%;

    justify-content: space-between;
}

#logoSection {
    padding: 0rem 2rem;
    font-size: 2rem;
    font-family: Inconsolata-Bold;
}

#navSelectionSection {
    display: flex;
    gap: 3rem;
    padding: 0rem 2rem;
    align-items: center;
    
}

#navOptions {
    display: flex;
    list-style: none;
    gap: 2rem;
}

#resumeButton {
    padding: 1rem 1.5rem;
    font-size: 1.35rem;
}

#landingPageContent {
    display: grid;
    grid-template-columns: 1fr 1fr;
   //background-color: blue;
    width: 95%;
    height: 80%;
    align-content: center;
    
    
}

#checkOutProjectButton {
    display: flex;
    padding: 1.5rem 2rem;
    font-size: 1.5rem;
    
}

#welcomeMessage {
    display: flex;
    justify-content: center;
    //background-color: purple;
}

#welcomeMessageContent{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    width: 90%;
    font-size: 2.35rem;
    justify-content: center;   
}

#welcomeMessageContent p {
    font-size: 1.7rem;
}

#welcomeImage {
    display: flex;
    justify-content: center;
    //background-color: yellow;
}

#welcomeImage img {
    max-width: 80%;
    
}